import { getSourceId, includesSourceId } from '@utils/foundry/flags'

type ActorItemTypes<A extends Actor> = (keyof A['itemTypes'])[]

function getItemSourceIdCondition(sourceId: ItemUUID | ItemUUID[]) {
    return Array.isArray(sourceId)
        ? (item: Item) => includesSourceId(item, sourceId)
        : (item: Item) => getSourceId(item) === sourceId
}

export function getItems<A extends Actor, I extends Item = CollectionValue<A['items']>>(actor: A, itemTypes?: ActorItemTypes<A>) {
    return (itemTypes ? itemTypes.flatMap(type => actor.itemTypes[type as string]) : actor.items) as Embedded<I>[]
}

export function findItemWithSourceId<A extends Actor, I extends Item = CollectionValue<A['items']>>(
    actor: A,
    sourceId: ItemUUID,
    itemTypes?: ActorItemTypes<A>
) {
    return getItems<A, I>(actor, itemTypes).find(getItemSourceIdCondition(sourceId))
}

export function hasItemWithSourceId<A extends Actor, I extends Item = CollectionValue<A['items']>>(
    actor: A,
    sourceId: ItemUUID | ItemUUID[],
    itemTypes?: ActorItemTypes<A>
) {
    return getItems<A, I>(actor, itemTypes).some(getItemSourceIdCondition(sourceId))
}

export function filterItemsWithSourceId<A extends Actor, I extends Item = CollectionValue<A['items']>>(
    actor: A,
    sourceId: ItemUUID | ItemUUID[],
    itemTypes?: ActorItemTypes<A>
) {
    return getItems<A, I>(actor, itemTypes).filter(getItemSourceIdCondition(sourceId))
}
